.wrapper {
  min-height: 100%;
  height: 100vh;
  display: grid;
  grid-template: minmax(100px, auto) 1fr minmax(70px, auto) / 1fr;
  grid-template-areas:
    "header"
    "main"
    "footer";
}
.header {
  grid-area: header;
  /* padding: 15px 0 0 0; */
  display: grid;
  grid-template: 1fr / 1fr minmax(auto, 1280px) 1fr;
}
.ul_header {
  background-color: rgb(255, 255, 255);
  border-bottom: solid 1px rgb(148, 148, 148);
  display: grid;
  grid-auto-flow: column;
  justify-items: center;
  align-items: center;
  grid-column: 2/3;
}
.item_logo {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  background-color: azure;
}
.main_section {
  grid-area: main;
  display: grid;
  grid-template: 1fr / 1fr minmax(auto, 1280px) 1fr;
}
.main_content {
  background-color: rgb(255, 255, 255);
  padding: 30px 15px;
  grid-column: 2/3;
}
.footer {
  grid-area: footer;
  display: grid;
  grid-template: 1fr / 1fr minmax(auto, 1280px) 1fr;
}

.footer_content {
  background-color: brown;
  padding: 15px;
  grid-column: 2/3;
}

.li_produkt {
  position: relative;
  padding: 10px;
  display: grid;
  grid-template: auto 1fr 20px 90px / auto;
  /* From https://css.glass */
  background: rgba(255, 255, 255, 0.14);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(0px);
  -webkit-backdrop-filter: blur(0px);
  border: 1px solid rgba(255, 255, 255, 0.36);
}
.img_produkt {
  top: 0;
  left: 0;
  width: 100%;
  /* height: 100%; */
  padding: 5% 0 15% 0;
  object-fit: cover;
}
.div_img_produkt {
  position: relative;
  display: flex;
  grid-row: 2 / 3;
  align-items: center;
  cursor: pointer;
}
.div_img_produkt::before {
  display: grid;
  content: attr(data-description);
  position: absolute;
  padding: 10px;
  top: 0;
  left: 0;
  width: calc(100% - 20px);
  height: 95%;
  align-items: center;
  transition: all 0.1s ease-out;
  grid-row: 2 / 3;
  visibility: hidden;
  z-index: -1;
  opacity: 0;
  /* From https://css.glass */
  background: rgba(255, 255, 255, 0.52);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(11px);
  -webkit-backdrop-filter: blur(11px);
  border: 1px solid rgba(255, 255, 255, 0.36);
}
.div_img_produkt:hover:before {
  opacity: 1;
  visibility: visible;
  z-index: 1;
}
